<template>
    <div class="shop-cart" v-loading="loading">
        <iframe :src="url" class="windows" ref="ifr" @load="onLoad"></iframe>
    </div>
</template>

<script>
    export default {
        name: "shopCart",
        data(){
            return {
                loading:true
            }
        },
        computed:{
            url: {
                get() {
                    return this.$C('serverUrlAjax') + "/exusers/u_cart.php?ismobile=1&idweb=" + this.$getWebId() + "&act=show&lang=" + this.$langID() + "&v=9";
                }
            }
        },
        created() {
            if(!this.$isMobile()){
                this.$router.push({name:"home"});
            }
        },
        methods: {
            onLoad() {
                this.loading = false;
            }
        }
    }
</script>

<style lang="scss" scoped>
    .shop-cart {
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
		position: absolute; 
		/*right: 0; 
		bottom: 0; 
		-webkit-overflow-scrolling: touch; 
		overflow-y: scroll;*/

        .windows {
            position: relative;
            width: 100%;
            height: 100%;
        }
    }
</style>